export default {
    layout: "Profile-content",
    data() {
        return {
            social: {
                social_name: "",
                social_url: "",

            },
            errors: [],
        };
    },
    methods: {
        addsocial() {

            this.$axios
                .$post("/social/add", this.social)
                .then((res) => {
                    this.social = "";
                    this.errors = "";
                    this.$toaster.success(res.message);
                    this.$router.push("/profile/social");
                })
                .catch((error) => {
                    this.errors = error.response.data.errors;
                    console.log(this.errors);
                });
        },
    },

};
